import {
  Button,
  Input,
} from "@ya.praktikum/react-developer-burger-ui-components";
import React, { useCallback, useState } from "react";
import { useNavigate } from "react-router-dom";
import { registerUser } from "../../services/redux/actions/user";
import { useDispatch } from "../../utils/hook";
import styles from "./register.module.css";

const RegistrationPage = () => {
  const [form, setForm] = React.useState({ name: "", email: "", password: "" });
  const inputRef = React.useRef<HTMLInputElement>(null);
  const navigate = useNavigate();
  const dispatch = useDispatch();

  const onChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    setForm({ ...form, [e.target.name]: e.target.value });
  };

  const handleSubmit = useCallback(
    (e: React.FormEvent<HTMLFormElement>) => {
      e.preventDefault();
      dispatch(registerUser(form, navigate));
    },
    [dispatch, form, navigate]
  );

  const [inputType, setInputType] = useState("password");

  const onIconClick = () => {
    setTimeout(() => inputRef.current?.focus(), 0);
    setInputType(inputType === "password" ? "text" : "password");
  };

  return (
    <div className={styles.wrapper}>
      <form onSubmit={handleSubmit} className={styles.container}>
        <h2 className={`${styles.heading} text text_type_main-medium`}>
          
        </h2>
        <Input
          type={"text"}
          placeholder={""}
          onChange={onChange}
          value={form.name}
          name={"name"}
          error={false}
          ref={inputRef}
          errorText={""}
          size={"default"}
        />
        <Input
          type={"email"}
          placeholder={"E-mail"}
          onChange={onChange}
          value={form.email}
          name={"email"}
          error={false}
          ref={inputRef}
          errorText={""}
          size={"default"}
        />
        <Input
          type={inputType as "password" | "text"}
          placeholder={""}
          onChange={onChange}
          value={form.password}
          name={"password"}
          error={false}
          icon={"ShowIcon"}
          ref={inputRef}
          onIconClick={onIconClick}
          errorText={""}
          size={"default"}
        />
        <Button type="primary" size="medium" htmlType={"submit"}>
          
        </Button>
        <div style={{ marginTop: 80, marginBottom: 16 }}>
          <span className="text text_type_main-default mr-2">
             ?
          </span>
          <Button
            onClick={() => {
              navigate("/login");
            }}
            type="secondary"
            size="medium"
            htmlType={"button"}
          >
            
          </Button>
        </div>
      </form>
    </div>
  );
};

export default RegistrationPage;
